@font-face {
  font-family: "Manrope-Regular";
  src: local("Manrope-Regular"), url("./Manrope-Regular.ttf") format("truetype");
  font-weight: 400;
}

@font-face {
  font-family: "Manrope-Medium";
  src: local("Manrope-Medium"), url("./Manrope-Medium.ttf") format("truetype");
  font-weight: 500;
}

@font-face {
  font-family: "Manrope-SemiBold";
  src: local("Manrope-SemiBold"),
    url("./Manrope-SemiBold.ttf") format("truetype");
  font-weight: 600;
}

@font-face {
  font-family: "Montserrat-Regular";
  src: local("Montserrat-Regular"),
    url("./Montserrat-Regular.ttf") format("truetype");
  font-weight: 400;
}

@font-face {
  font-family: "Montserrat-SemiBold";
  src: local("Montserrat-SemiBold"),
    url("./Montserrat-SemiBold.ttf") format("truetype");
  font-weight: 600;
}

@font-face {
  font-family: "DancingScript-Regular";
  src: local("DancingScript-Regular"),
    url("./DancingScript-Regular.ttf") format("truetype");
  font-weight: 400;
}

.ManropeR {
  font-family: "Manrope-Regular", sans-serif;
}

.ManropeM {
  font-family: "Manrope-Medium", sans-serif;
}

.ManropeB {
  font-family: "Manrope-SemiBold", sans-serif;
}

.MontserratR {
  font-family: "Montserrat-Regular", sans-serif;
}

.MontserratB {
  font-family: "Montserrat-SemiBold", sans-serif;
}

.DancingScriptR {
  font-family: "DancingScript-Regular", cursive;
}
